h2 {
  margin: 0;
}

.breed-select {
  min-width: 150px;
  border-radius: 5px;
  margin: 50px;
  padding: 5px;
}

.cat-info {
  max-width: 300px;
  background-color: bisque;
  border-radius: 5px;
  flex-direction: column;
  gap: 30px;
  margin: auto;
  padding: 30px;
  display: flex;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.js-cat-description {
  max-width: 600px;
}

.js-cat-photo {
  height: 100%;
}

.loader, .error {
  color: red;
  font-size: 24px;
}

@media screen and (min-width: 768px) {
  .cat-info {
    max-width: 900px;
    flex-direction: row;
    margin: 50px;
  }
}

@media screen and (max-width: 360px) {
  .js-cat-photo {
    width: 250px;
  }
}

